.search-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 10px;
}

.search-input {
    padding: 8px 15px;
    font-size: 15px;
    width: 250px;
    border-width: 0px;
    background: #a592f2;
    color: #e7e9f0;
    box-shadow: 0 0 5px rgba(255, 255, 0.5);

}

.search-input::placeholder {
    color: #e7e9f0;
}